// main.sass
@import "reset";
@import url('https://fonts.googleapis.com/css?family=Titillium+Web:400,600');

// @VARIABLES


// @ANIMATION
@keyframes slideup {
	0% {
		top: 100px; }
	80% {
		top: 0; }
	100% {
		top: -100px; } }

// @GLOBAL
html,
body {
	font-size: 10px;
	font-family: 'Titillium Web', sans-serif;
	height: 100%;
	width: 100%;
	background-color: transparent;
	background-repeat: repeat;
	background-attachment: fixed;
	color: black; }

body {
	display: grid;
	grid-template-columns: 100%;
	grid-template-rows: auto auto auto;
	grid-template-areas: "headerarea" "mainarea" "footerarea";
	grid-row-gap: 20px;
	justify-content: stretch;
	header {
		display: flex;
		grid-area: headerarea;
		grid-column: auto;
		grid-row: 1 / 2;
		justify-content: space-between;
		align-items: baseline;
		h1 {
			flex: 1 1 auto;
			line-height: 0;
			svg {
				max-height: 100px;
				max-width: 210px;
				background-color: transparent;
				padding: 0 20px;
				polygon#svgY,
				polygon#svgD {
					fill: black; } } }
		nav {
			flex: 0 1 auto;
			text-align: right;
			ul {
				display: flex;
				justify-content: flex-end;
				li {
					flex: 0 1 auto;
					font-size: 200%;
					a {
						background-color: black;
						text-decoration: none;
						color: white;
						padding: 10px 25px;
						text-transform: uppercase;
						font-weight: 600; }
					a:hover {
						color: silver; } } } } }
	@media (max-width: 800px), (orientation: portrait) {
		header {
			align-items: center;
			h1 {
				height: 100%;
				padding: 0;
				svg {
					max-height: 50px; } } } }
	main {
		grid-area: mainarea;
		grid-column: auto;
		grid-row: 2 / 3;
		h2#sorry-the-requested-page-wasnt-found-on-the-server {
			font-size: 300%;
			text-align: center;
			background-color: black;
			color: white;
			padding: 10px 25px;
			text-transform: uppercase;
			font-weight: 600; } }
	footer {
		grid-area: footerarea;
		grid-column: auto;
		grid-row: 3 / auto;
		margin-top: 40px;
		hr {
			width: 33%;
			border: solid 0px transparent;
			margin-bottom: 20px; }
		ul {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 20px;
			li {
				flex: 0 0 auto;
				margin: 0 10px;
				svg {
					max-height: 80px; }
				svg#datalove g {
					fill: white;
					font-size: 30px;
					text-anchor: middle;
					font-weight: 900;
					font-family: 'DejaVu Sans', Verdana, Geneva, sans-serif; }
				svg#datalove:hover text {
					fill: red; }
				a {
					font-weight: 600;
					color: black;
					text-decoration: none;
					font-size: 1.4em;
					text-transform: uppercase; } } } }

	section {
		padding: 0 20px; }
	div#data {
		padding: 0 20px;
		margin: 40px 0;
		p {
			text-transform: uppercase;
			font-size: 800%;
			font-weight: 600;
			overflow: hidden;
			padding-bottom: 24px;
			span.wrapper {
				border-bottom: solid 3px black;
				z-index: 100;
				span.colorset-0 {
					background: linear-gradient(45deg, cyan, magenta);
					background-clip: text;
					-webkit-text-fill-color: transparent; }
				span.colorset-1 {
					background: linear-gradient(45deg, lime, orange);
					background-clip: text;
					-webkit-text-fill-color: transparent; }
				span.colorset-2 {
					background: linear-gradient(45deg, purple, aqua);
					background-clip: text;
					-webkit-text-fill-color: transparent; }
				span.colorset-3 {
					background: linear-gradient(45deg, orange, purple);
					background-clip: text;
					-webkit-text-fill-color: transparent; }
				span.colorset-4 {
					background: linear-gradient(45deg, #FF3912, #E81411, #FF1F82, #E811DB);
					background-clip: text;
					-webkit-text-fill-color: transparent; }
				span {
					position: relative;
					z-index: 1;
					top: 100px;
					font-weight: 600; }
				span.slideup {
					animation: 2s slideup; } } } }

	div#datalove {
		ul {
			margin-left: 20px;
			li {
				font-size: 220%;
				margin-bottom: 5px; }
			li:first-of-type,
			li:last-of-type {
				color: red;
				font-weight: 600;
				text-transform: uppercase; } } }

	section#aboutme {
		h2 {
			text-transform: uppercase;
			font-size: 400%;
			font-weight: 600; }
		p {
			font-size: 150%; }
		article#contact {
			p {
				font-size: 150%;
				margin: 5px 0; } }
		h3 {
			text-transform: uppercase;
			font-size: 200%;
			margin: 20px 0;
			line-height: 1.5;
			a {
				color: black;
				text-decoration: none;
				padding: 0 10px;
				border-bottom: solid #000000 3px; }
			a[href^="//github"] {
				border-bottom: solid #0366d6 3px; }
			a[href^="//linkedin"] {
				border-bottom: solid #0091ca 3px; } } }
	section#experiences,
	section#education,
	section#website {
		article {
			margin: 40px 0; }
		h2 {
			text-transform: uppercase;
			font-size: 350%;
			font-weight: 600;
			margin: 20px 0 10px;
			span {
				padding: 5px 10px;
				background-color: black;
				color: white; } }
		h3 {
			span {
				font-weight: 600; } }
		.timeframe {
			margin-bottom: 10px; }
		.content {
			border-left: 2px solid black;
			margin-left: 25px;
			li {
				padding-left: 20px;
				font-size: 200%; } }
		p {
			padding-left: 20px;
			font-size: 200%; }
		article {
			h3 {
				text-transform: uppercase;
				font-size: 250%;
				padding-left: 20px;
				margin: 5px 0 10px; }
			h3:first-of-type {
				margin: 20px 0 5px; }
			p {
				padding-left: 20px;
				font-size: 200%; } } }
	ul#projlist {
		margin-top: 20px;
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
		li {
			flex: 0 0 auto;
			margin: 20px;
			a {
				display: block;
				text-decoration: none;
				background-color: rgba(55,55,55,0);
				span {
					font-size: 260%;
					text-decoration: none;
					text-transform: uppercase;
					font-weight: 600;
					color: white;
					background-color: black;
					padding: 5px 10px; }
				@media (orientation: landscape) {
					span {
						font-size: 300%; } } } } }

	section#descriptions {
		display: none;
		background-color: rgba(0, 0, 0, 0.95);
		position: fixed;
		box-sizing: border-box;
		width: 800px;
		height: 500px;
		top: calc(50% - (500px / 2));
		left: calc(50% - (800px / 2));
		text-align: right;
		#close {
			background-color: transparent;
			border: none;
			margin-top: 30px;
			img {
				width: 30px;
				height: 30px; } }
		article {
			display: none;
			padding: 40px;
			h2 {
				font-size: 260%;
				text-transform: uppercase;
				color: white;
				font-weight: 600; }
			p {
				margin: 10px 0;
				font-size: 160%;
				color: #CCCCCC; }
			.links {
				margin-top: 40px;
				p.link {
					text-align: right;
					margin: 15px 0;
					a {
						background-color: silver;
						text-decoration: none;
						span {
							padding: 5px 10px;
							color: black; } } } } } }
	@media (max-width: 800px), (orientation: portrait) {
		section#descriptions {
			top: 0;
			left: 0;
			width: 100%;
			height: 100%; } }

	section#descriptions.visible {
		display: block;
		article.visible {
			display: block; } } }

body#about {
	main {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-template-row: 1fr 1fr;
		section#aboutme {
			grid-column: 1 / 2;
			grid-row: 1 / 1; }
		section#experiences {
			grid-column: 1 / 1;
			grid-row: 2 / 2; }
		section#education {
			grid-column: 2 / 2;
			grid-row: 2 / 2; }
		@media (max-width: 800px), (orientation: portrait) {
			section#aboutme {
				width: calc(100vw - 40px);
				grid-column: 1 / 2;
				grid-row: 1 / 1; }
			section#experiences {
				width: calc(100vw - 40px);
				grid-column: 1 / 2;
				grid-row: 2 / 2; }
			section#education {
				width: calc(100vw - 40px);
				grid-column: 1 / 2;
				grid-row: 3 / 3; } } } }

// @PARTIALS

@import "cube";
